/* # Global */
:root {
 --text-main-color: #1A202C; /* Gray 900 */
 --text-second-color: #fff; /* Blanc */
 --text-third-color: #4A5568; /* Gray 700 */
 
 --bg-main1-color: #fff; /* Blanc */
 --bg-main2-color: #edf2f7; /* Gray 200 */

 --main-doc-color: #0096e6; /* Couleur de la doc */
}
*, *:before, *:after {
  box-sizing: border-box;
}
html {
  font-size: 16px;
}
body {
  font-family: 'Lato', sans-serif;
  font-size: 1rem;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
h1 {
  margin: 0 0 1.5rem 0;
  font-size: 2rem;
  font-weight: 900;
}
h2, .heading-2 {
  margin: 0 0 1.5rem 0;
  font-size: 1.5rem;
  font-weight: 700;
}
h3, .heading-3 {
  margin: 0 0 1.25rem 0;
  font-size: 1.125rem;
  font-weight: 700;
}
h4, .heading-4 {
  margin: 0 0 1rem 0;
  font-size: 1rem;
  font-weight: 700;
}
p {
  margin: 0 0 1.5rem 0;
  line-height: 1.25rem;
}
a {
  text-decoration: none;
  color: #1A202C;
  color: var(--text-main-color);
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

/* ## Class */
.flex-items-center {
  display: flex;
  align-items: center;
}
.row {
  margin-bottom: 3rem;
}
.row:last-child {
  margin-bottom: 0;
}
.closeButton:before {
  content:'\e906';
  display: block;
  font-family: 'documentation-ico';
  cursor: pointer;
}
.closeButton:hover:before {
  transform: rotate(90deg);
}
button {
  padding: .25rem .75rem;
  font-weight: 400;
  font-size: .75rem;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

/* # Header */
.mainHeader {
  display: flex;
  width: 100%;
  height: 60px;
  align-items: center;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 9999;
}
.sidebarHeader {
  width: 23.75rem;
  padding: .375rem 0 .375rem 2.25rem;
}
.sidebarHeader img {
  height: 30px;
  margin-right: 1rem;
}
.sidebarHeader .appType {
  padding: .175rem .375rem;
  font-size: .75rem;
  color: #4A5568;
  color: var(--text-third-color);
}

/* ## Nav */
.mainNav li a {
  height: 2.5rem;
  margin-right: 1rem;
  padding: .5rem .875rem;
  font-size: .875rem;
}
.mainNav li:last-child a  {
  margin-right: 0;
}
.mainNav li a:before {
  margin-right: .875rem;
  font-family: 'documentation-ico';
  font-size: 1rem;
  font-weight: 400;
}
li.menuHome a:before {
  content: '\e901';
}
li.menuModeles a:before {
  content: '\e900';
}

/* ## Searchbox */
.searchBox {
  width: 100%;
  max-width: 400px;
  margin-left: auto;
  padding-right: 2.25rem;
}
.searchBox form input {
  width: 100%;
  height: 2.5rem;
  padding: .75rem 2.5rem .75rem 1rem;
  font-size: 1rem;
  border: none;
}
.searchBox form input:focus {
  outline: none;
}
.searchBox form button {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  background: none;
}
.searchBox form button:before {
  content: '\e904';
  font-family: 'documentation-ico';
  font-size: 1rem;
}

/* # Sidebar */
.sidebar {
  display: flex;
  width: 23.75rem;
  height: calc(100vh - 60px);
  padding: 3rem 0 0 2.25rem;
  flex-direction: column;
  position: fixed;
  top: 60px;
  left: 0;
  z-index: 9999;
}
.themsList .logoThem, .currentThem .logoThem {
  width: 1.5rem;
  margin-right: 1rem;
}

/* ## Account */
.account {
  margin: auto 2rem 2rem 0;
  padding: 1.25rem;
}
.accountMail {
  word-break: break-all;
}
.account .accountOuter:after {
  content: '\e905';
  margin-left: auto;
  font-family: 'documentation-ico';
}
.account.Open .accountOuter:after {
  content: '\e906';
}

/* # App Main */
.appMain {
  margin-left: 23.75rem;
}
.mainInner.row {
  min-height: calc(100vh - 60px - 2rem);
  margin: 60px 0 2rem;
  padding: 3rem 1.5rem;
}

/* ## Thèmes */
.themInfo {
  display: flex;
  flex-direction: column;
}
.themInfo .logoThem {
  width: 2.5rem;
  margin-bottom: 1rem;
}
.nbUC {
  margin-top: auto;
  color: #4A5568;
  color: var(--text-third-color);
}

/* # UC list */
.ucList .itemUC {
  padding-bottom: 1rem;
}
.ucList .itemUC:last-child {
  padding-bottom: 0;
}
.itemUCOuter:before {
  content: "\e900";
  font-family: "documentation-ico";
  font-size: 1.25rem;
  padding-right: 1rem;
}
.titleUC {
  padding-bottom: .25rem;
  font-size: .875rem
}
.refInternet {
  font-size: .75rem;
}

